<template>
  <div class="slide" :class="{'is-show': isShow}">
    <div class="slide-heading">
      <slot name="component"></slot>
      <p @click="show">显示 / 隐藏代码</p>
    </div>
    <div class="slide-content" :class="{'is-show': isShow}"> 
      <slot name="code"></slot>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        isShow: false
      }
    },
    methods: {
      show() {
        this.isShow = !this.isShow
      }
    }
  }
</script>

<style lang="scss">
  .slide {
    .slide-heading {
      width: 600px;
      padding: 20px;
      padding-bottom: 0;
      cursor: pointer;
      border: 1px solid #ebebeb;
      p {
        margin-top: 20px;
        border-top: 1px solid #ebebeb;
        padding: 20px;
        text-align: center
      }
    }

    .slide-content {
      height: 0;
      overflow: hidden;
      background-color: #fafafa;
      color: #67C23A
    }
    .is-show {
      height: auto;
      padding: 20px;
      border: 1px solid #ebebeb;
      border-top: 0;
      transition: height 1s
    }
  }
</style>